  --black: #040403ff;
  --primary: #696969;
  --primary-500: #9b9b9b;
  --primary-250: #bbbbbb;
  --secondary: #82b08c;
  --accent: #9ddbad;
  --accent-second: #c3e8bd;
  --warning: #de6868;
  --warning-500: #db9d9d;

  --bg: #f6f6f6;
  --bg-light: #fafafa;
  --bg-md: #f0f0f0;
  --bg-dark: #eaeaea;

  --space-half: 4px;
  --space-1x: 8px;
  --space-2x: 16px;
  --space-3x: 24px;
  --space-4x: 32px;
  --space-5x: 40px;
  --space-6x: 48px;
  --space-7x: 56px;
  --space-10x: 80px;

  --icon-xl: 80px;
  --icon-lg: 60px;
  --icon-md: 40px;
  --icon-sm: 32px;
  --icon-xs: 24px;
}
* {
  box-sizing: border-box;
  margin: 0px;
}
body {
  font-family: "Outfit", sans-serif;
  font-weight: 400;

  height: 100vh;
  color: var(--primary);
  background-color: var(--bg-dark);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

/* Fonts */
h1,
h2,
h3,
h4,
h5 {
  font-family: "Outfit", sans-serif;
  font-weight: 700;
}

html {
  font-size: 100%;
} /* 16px */

h1 {
  font-size: 2.489rem; /* 39.84px */
}

h2,
.h2 {
  font-size: 2.074rem; /* 33.12px */
}

h3,
.h3 {
  font-size: 1.728rem; /* 27.68px */
}

h4,
.h4 {
  font-size: 1.44rem; /* 23.04px */
}

h5,
.h5 {
  font-size: 1.2rem; /* 19.2px */
}

small,
.small {
  font-size: 0.833rem; /* 13.28px */
}

.icon-xl {
  width: 80px;
}
.icon-lg {
  width: 60px;
}
.icon-md {
  width: 40px;
}
.icon-sm {
  width: 32px;
}
.icon-xs {
  width: 20px;
}

.temp-display {
  font-size: 5rem;
  margin: 0;
}

/* Main container */
.main-container {
  width: 1150px;
  background-color: var(--bg-light);
  border-radius: var(--space-3x);
  padding: var(--space-5x);
  margin: var(--space-3x);

  display: flex;
  flex-direction: column;
}

/* Nav */
nav {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: var(--space-4x);
}

nav .search-form {
  display: flex;
  align-items: center;
  gap: var(--space-1x);
}

.logo-name-wrapper {
  display: flex;
  align-items: center;
  gap: var(--space-half);
}

.logo-name-wrapper > img {
  width: var(--icon-xs);
}
.app-name {
  font-weight: 600;
  font-size: 1.2rem;
}

.search-btn {
  border: 0px;
  border-radius: var(--space-1x);
  padding: var(--space-1x);
  background-color: inherit;
}

.search-btn:hover {
  background-color: var(--bg-md);
  cursor: pointer;
}

nav input {
  font-family: inherit;
  color: inherit;
  font-size: 100%;
  background-color: white;
  border: 1px solid var(--primary-500);
  border-radius: var(--space-2x);
  padding: var(--space-1x);
}

nav input:focus-visible {
  outline: 1px solid var(--primary);
}
/* Nav unit selector */
input[type="radio"] {
  appearance: none;
  display: none;
}

.temp-selector {
  display: flex;
  gap: var(--space-1x);
}

.radio-label {
  cursor: pointer;
  font-weight: 400;
  border-radius: var(--space-1x);
  padding: var(--space-1x);
}

.radio-label.checked {
  background-color: var(--bg-md);
}

/* dashboard */
.dashboard-wrapper {
  display: flex;
  justify-content: space-between;
  gap: var(--space-3x);
  margin-top: var(--space-1x);
}

aside {
  max-height: 100%;
  display: flex;
  flex-direction: column;
  width: 230px;
}
/* Location */
.location-wrapper {
  text-align: center;
}

.forecast-location {
  margin-bottom: var(--space-1x);
  font-weight: 500;
}

/* Forecast wrapper */
.forecast-wrapper {
  margin: var(--space-4x) 0 var(--space-6x);

  display: grid;
  align-items: center;
  grid-template-columns: 2fr 1fr;
  gap: var(--space-4x);
}

.icon-display {
  width: var(--icon-xl);
}
.forecast-main {
  display: flex;
  flex-direction: column;
}
.forecast-wrapper .limit-temp-forecast {
  align-self: center;
  display: flex;
  gap: var(--space-2x);
  margin: var(--space-1x) 0;
}
/* temp wrapper  */
.temp-wrapper {
  display: grid;
  grid-template-columns: 1fr 2fr;
  align-items: center;
  gap: var(--space-4x);
}

.temp-wrapper > img {
  justify-self: end;
}

.forecast-desc {
  text-align: center;
  min-height: 40px;
  max-width: 509px;
}
/* Forecast element */
.detail-wrapper {
  display: flex;
  align-items: center;
  gap: var(--space-1x);
}
.forecast-details > div:not(:last-child) {
  margin-bottom: var(--space-2x);
}

/* Week forecast   */
.week-forecast {
  display: flex;
  /* justify-content: space-between; */
  flex-wrap: wrap;
  gap: var(--space-2x);
}
.day-forecast {
  width: 100px;
  border: 1px solid var(--primary-500);
  border-radius: var(--space-2x);
  padding: var(--space-2x);
  text-align: center;
  cursor: pointer;
}
.day-date {
  color: var(--primary-500);
  margin-top: var(--space-half);
}
.day-forecast > .icon-md {
  padding: var(--space-1x) 0;
}
.day-forecast-temp {
  font-weight: bold;
  font-size: larger;
}
/* Hours wrapper */
.hourly-title {
  font-weight: 500;
  margin-bottom: var(--space-2x);
}
.hours-forecast {
  display: flex;
  flex-direction: column;
  gap: var(--space-2x);
  max-height: 460px;
  overflow-x: hidden;
  overflow-y: auto;

  padding-right: var(--space-4x);
}
.hour-forecast {
  display: grid;
  grid-template-columns: 1fr 32px 1fr;
  align-items: center;
  gap: var(--space-4x);
  /* padding-right: ; */
}

.hour-forecast div:nth-child(4),
.hour-forecast div:nth-child(5) {
  display: none;
}

.hour {
  width: 45px;
  text-align: right;
}
.hour-temp {
  text-align: right;
}

/* Hours section scrollbar styling */
.hours-forecast::-webkit-scrollbar {
  width: 10px;
}
.hours-forecast::-webkit-scrollbar-track {
  background: var(--bg-md);
  border-radius: var(--space-6x);
}
.hours-forecast::-webkit-scrollbar-thumb {
  background: var(--primary-250);
  border-radius: var(--space-6x);
}
/* Error box */

.error-box {
  text-align: center;
  margin-top: var(--space-1x);
  color: var(--warning);
}
.hidden {
  visibility: hidden;
}

footer {
  position: absolute;
  bottom: 0px;

  grid-column: 1 / -1;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  padding: var(--space-2x);
}
footer a {
  color: var(--primary);
  font-weight: 600;
}
@media screen and (max-width: 1180px) {
  body {
    height: auto;
    flex-direction: column;
  }
  .main-container {
    width: 878px;
    margin-bottom: 0px;
  }
  footer {
    position: static;
  }
  .dashboard-wrapper {
    flex-wrap: wrap;
    justify-content: center;
    row-gap: var(--space-5x);
  }

  .aside {
    justify-self: center;
  }
  aside {
    width: 480px;
  }
  .hours-forecast {
    padding-right: 0px;
  }
  .hour-forecast {
    display: grid;
    grid-template-columns: 1fr 32px 1fr 3fr 3fr;
  }
  .hour-forecast div:nth-child(4),
  .hour-forecast div:nth-child(5) {
    display: flex;
  }
}

@media screen and (max-width: 925px) {
  .main-container {
    width: 620px;
  }
  .week-forecast {
    display: grid;
    row-gap: var(--space-3x);
    grid-template-columns: repeat(4, 1fr);
    justify-items: center;
  }
  .temp-display {
    font-size: 3.5rem;
  }
}

@media screen and (max-width: 668px) {
  nav {
    gap: var(--space-2x);
  }
  .temp-display {
    font-size: 3rem;
  }
  .icon-display {
    width: var(--icon-lg);
  }
}

@media screen and (max-width: 602px) {
  .forecast-wrapper {
    grid-template-columns: 1fr;
  }
  .forecast-details {
    display: grid;
    column-gap: var(--space-2x);
    /* grid-template-columns: repeat(2, 180px); */
    grid-template-columns: repeat(auto-fill, minmax(min(180px, 100%), 1fr));
    justify-content: center;
  }
  .main-container {
    width: auto;
  }
  aside {
    max-width: 230px;
  }
  .hours-forecast {
    padding-right: var(--space-3x);
  }
  .hour-forecast {
    display: grid;
    grid-template-columns: 1fr 32px 1fr;
  }
  .hour-forecast div:nth-child(4),
  .hour-forecast div:nth-child(5) {
    display: none;
  }

  .search-form {
    width: 100%;
  }
  #search {
    width: 100%;
    min-width: 0px;
  }
}

@media screen and (max-width: 576px) {
  main {
    width: 100%;
  }
  .week-forecast {
    grid-template-columns: repeat(auto-fill, minmax(min(180px, 100%), 1fr));
    gap: var(--space-2x);
  }
  .day-forecast {
    display: grid;
    grid-template-columns: 1fr 40px 1fr;
    width: 100%;
    align-items: center;
    column-gap: var(--space-1x);
  }
  .day-forecast .icon-md {
    grid-column: 2 / 3;
    grid-row: 1 / 3;
  }
  .day-forecast .day-temp {
    grid-column: 3 / 4;
    grid-row: 1 / 3;
  }
}

@media screen and (max-width: 503px) {
  nav {
    flex-wrap: wrap;
  }
  .search-form {
    width: 100%;
  }
  #search {
    width: 100%;
    min-width: 0px;
  }
  .forecast-details {
    justify-items: center;
  }
  .temp-wrapper {
    gap: var(--space-2x);
  }
}

@media screen and (max-width: 380px) {
  nav {
    flex-direction: column;
    align-items: center;
  }
  .main-container {
    padding: var(--space-4x);
  }
  .temp-display {
    font-size: 2.5rem;
  }
  .forecast-location {
    font-size: 1.2rem;
  }
  aside {
    width: 100%;
    max-width: none;
  }
}
