th: 60px;\n}\n.icon-md {\n  width: 40px;\n}\n.icon-sm {\n  width: 32px;\n}\n.icon-xs {\n  width: 20px;\n}\n\n.temp-display {\n  font-size: 5rem;\n  margin: 0;\n}\n\n/* Main container */\n.main-container {\n  width: 1150px;\n  background-color: var(--bg-light);\n  border-radius: var(--space-3x);\n  padding: var(--space-5x);\n  margin: var(--space-3x);\n\n  display: flex;\n  flex-direction: column;\n}\n\n/* Nav */\nnav {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  gap: var(--space-4x);\n}\n\nnav .search-form {\n  display: flex;\n  align-items: center;\n  gap: var(--space-1x);\n}\n\n.logo-name-wrapper {\n  display: flex;\n  align-items: center;\n  gap: var(--space-half);\n}\n\n.logo-name-wrapper > img {\n  width: var(--icon-xs);\n}\n.app-name {\n  font-weight: 600;\n  font-size: 1.2rem;\n}\n\n.search-btn {\n  border: 0px;\n  border-radius: var(--space-1x);\n  padding: var(--space-1x);\n  background-color: inherit;\n}\n\n.search-btn:hover {\n  background-color: var(--bg-md);\n  cursor: pointer;\n}\n\nnav input {\n  font-family: inherit;\n  color: inherit;\n  font-size: 100%;\n  background-color: white;\n  border: 1px solid var(--primary-500);\n  border-radius: var(--space-2x);\n  padding: var(--space-1x);\n}\n\nnav input:focus-visible {\n  outline: 1px solid var(--primary);\n}\n/* Nav unit selector */\ninput[type=\"radio\"] {\n  appearance: none;\n  display: none;\n}\n\n.temp-selector {\n  display: flex;\n  gap: var(--space-1x);\n}\n\n.radio-label {\n  cursor: pointer;\n  font-weight: 400;\n  border-radius: var(--space-1x);\n  padding: var(--space-1x);\n}\n\n.radio-label.checked {\n  background-color: var(--bg-md);\n}\n\n/* dashboard */\n.dashboard-wrapper {\n  display: flex;\n  justify-content: space-between;\n  gap: var(--space-3x);\n  margin-top: var(--space-1x);\n}\n\naside {\n  max-height: 100%;\n  display: flex;\n  flex-direction: column;\n  width: 230px;\n}\n/* Location */\n.location-wrapper {\n  text-align: center;\n}\n\n.forecast-location {\n  margin-bottom: var(--space-1x);\n  font-weight: 500;\n}\n\n/* Forecast wrapper */\n.forecast-wrapper {\n  margin: var(--space-4x) 0 var(--space-6x);\n\n  display: grid;\n  align-items: center;\n  grid-template-columns: 2fr 1fr;\n  gap: var(--space-4x);\n}\n\n.icon-display {\n  width: var(--icon-xl);\n}\n.forecast-main {\n  display: flex;\n  flex-direction: column;\n}\n.forecast-wrapper .limit-temp-forecast {\n  align-self: center;\n  display: flex;\n  gap: var(--space-2x);\n  margin: var(--space-1x) 0;\n}\n/* temp wrapper  */\n.temp-wrapper {\n  display: grid;\n  grid-template-columns: 1fr 2fr;\n  align-items: center;\n  gap: var(--space-4x);\n}\n\n.temp-wrapper > img {\n  justify-self: end;\n}\n\n.forecast-desc {\n  text-align: center;\n  min-height: 40px;\n  max-width: 509px;\n}\n/* Forecast element */\n.detail-wrapper {\n  display: flex;\n  align-items: center;\n  gap: var(--space-1x);\n}\n.forecast-details > div:not(:last-child) {\n  margin-bottom: var(--space-2x);\n}\n\n/* Week forecast   */\n.week-forecast {\n  display: flex;\n  /* justify-content: space-between; */\n  flex-wrap: wrap;\n  gap: var(--space-2x);\n}\n.day-forecast {\n  width: 100px;\n  border: 1px solid var(--primary-500);\n  border-radius: var(--space-2x);\n  padding: var(--space-2x);\n  text-align: center;\n  cursor: pointer;\n}\n.day-date {\n  color: var(--primary-500);\n  margin-top: var(--space-half);\n}\n.day-forecast > .icon-md {\n  padding: var(--space-1x) 0;\n}\n.day-forecast-temp {\n  font-weight: bold;\n  font-size: larger;\n}\n/* Hours wrapper */\n.hourly-title {\n  font-weight: 500;\n  margin-bottom: var(--space-2x);\n}\n.hours-forecast {\n  display: flex;\n  flex-direction: column;\n  gap: var(--space-2x);\n  max-height: 460px;\n  overflow-x: hidden;\n  overflow-y: auto;\n\n  padding-right: var(--space-4x);\n}\n.hour-forecast {\n  display: grid;\n  grid-template-columns: 1fr 32px 1fr;\n  align-items: center;\n  gap: var(--space-4x);\n  /* padding-right: ; */\n}\n\n.hour-