 width: var(--icon-lg);\n  }\n}\n\n@media screen and (max-width: 602px) {\n  .forecast-wrapper {\n    grid-template-columns: 1fr;\n  }\n  .forecast-details {\n    display: grid;\n    column-gap: var(--space-2x);\n    /* grid-template-columns: repeat(2, 180px); */\n    grid-template-columns: repeat(auto-fill, minmax(min(180px, 100%), 1fr));\n    justify-content: center;\n  }\n  .main-container {\n    width: auto;\n  }\n  aside {\n    max-width: 230px;\n  }\n  .hours-forecast {\n    padding-right: var(--space-3x);\n  }\n  .hour-forecast {\n    display: grid;\n    grid-template-columns: 1fr 32px 1fr;\n  }\n  .hour-forecast div:nth-child(4),\n  .hour-forecast div:nth-child(5) {\n    display: none;\n  }\n\n  .search-form {\n    width: 100%;\n  }\n  #search {\n    width: 100%;\n    min-width: 0px;\n  }\n}\n\n@media screen and (max-width: 576px) {\n  main {\n    width: 100%;\n  }\n  .week-forecast {\n    grid-template-columns: repeat(auto-fill, minmax(min(180px, 100%), 1fr));\n    gap: var(--space-2x);\n  }\n  .day-forecast {\n    display: grid;\n    grid-template-columns: 1fr 40px 1fr;\n    width: 100%;\n    align-items: center;\n    column-gap: var(--space-1x);\n  }\n  .day-forecast .icon-md {\n    grid-column: 2 / 3;\n    grid-row: 1 / 3;\n  }\n  .day-forecast .day-temp {\n    grid-column: 3 / 4;\n    grid-row: 1 / 3;\n  }\n}\n\n@media screen and (max-width: 503px) {\n  nav {\n    flex-wrap: wrap;\n  }\n  .search-form {\n    width: 100%;\n  }\n  #search {\n    width: 100%;\n    min-width: 0px;\n  }\n  .forecast-details {\n    justify-items: center;\n  }\n  .temp-wrapper {\n    gap: var(--space-2x);\n  }\n}\n\n@media screen and (max-width: 380px) {\n  nav {\n    flex-direction: column;\n    align-items: center;\n  }\n  .main-container {\n    padding: var(--space-4x);\n  }\n  .temp-display {\n    font-size: 2.5rem;\n  }\n  .forecast-location {\n    font-size: 1.2rem;\n  }\n  aside {\n    width: 100%;\n    max-width: none;\n  }\n}\n"],"sourceRoot":""}]);
